.TopNav-root
{
    background-color:#303841;
    border-bottom: 1px solid #303841;
    transition: height 0.35s, background-color 0.35s, font-size 0.35s;
    height:80px;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
    display:flex;
    align-items: center;
}
.TopNav
{
    color:#ffffff;
    text-decoration: none; /*remove underline of hyper-link*/
}


.row-table
{
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    float: left;
    margin: 0;
}
.row-table li
{
    list-style-type: none;
    margin-left: 20px;
}

.inner-text
{
    letter-spacing: 2px;
    word-spacing: 0px;
    padding:0;
}

#main-logo
{
    font-weight: 700;
    font-size: 27px;
    color:#ffffff;
    font-family: 'Open Sans',Arial,sans-serif;
}

.container
{
    display: flex;
    justify-content: flex-end;
    align-content: center;
    align-items: center;
    flex-basis: auto;
    flex-grow: 1;
    float: left;
    margin: 0;
    text-transform: uppercase !important;
}

.container-right
{
    order: 999;
}

.menu-text {
    opacity: 1;
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    color:inherit;
}

.menu-activated
{
    color:#00adb5;
}

.menu-default
{
    color:#ffffff;
}

/* for TopScreen.js */

.top-screen
{
    background-color: black;
    left: 0;
    top: 0;
    height: 653px;
    min-height: 100% !important;
    display: flex;
}

.top-screen-image
{
    position: absolute;
    display: table-cell;
    vertical-align: middle;
    opacity: 0.5;
    width:100%;
    height:100%;
}
/* for MainContainer.js */
.main-container
{
    width: 100%;
    height: 100%;
}